<template>
	<div id="userCollect">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" style="color: #000000 !important;" @click="$router.back(-1)"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title">我的收藏</div>
      </div>
    </div>
    <div class="login_box">
      <van-list v-model="loading" :finished="finished" @load="getCollectList" :offset="100">
        <van-card v-for="(item, index) in collectList" :key="index" style="margin: 10px;" @click.native="toGoods(item)" v-if="item.goods">
          <div slot="thumb">
            <img :src="item.goods.goodslogo.split(',')[0]" v-lazy="item.goods.goodslogo.split(',')[0]" width="100%" style="display: block;" />
          </div>
          <div slot="tags" class="font_weight_bold padding_bottom_5">
            {{item.goods.goodsname}}
          </div>
          <div slot="tags" class="font_12 color_8b8b8b padding_bottom_5" style="height: 28px;overflow: auto">
            {{item.goods.introduce}}
          </div>
          <div slot="tags" class="font_12 color_8b8b8b" style="position: relative;top: 10px;">
            <span class="color_c10804" v-if="item.goods.type==1">¥{{item.goods.price}}</span>
            <span class="color_c10804" v-if="item.goods.type==2">¥{{item.goods.spike}}</span>
            <span class="float_right">{{item.goods.paynum}}人付款</span>
          </div>
        </van-card>
      </van-list>
    </div>
	</div>
</template>
<script>
	export default {
		name: 'userCollect',
		data() {
			return {
        loading: false,
        finished: false,
        pageNum: 1,
			  collectList:[],
        userId:localStorage.getItem('userId'),
			}
		},
    mounted() {
      this.getCollectList();
    },
		methods:{
      toGoods(dataMap){
        this.$store.commit('setGoodsInfo', dataMap);
        this.$router.push('goods');
      },
      getCollectList(){
        setTimeout(() => {
          this.loading = true;
          let $this = this;
          let obj = {
            userid: $this.userId,
            pageNum: $this.pageNum
          };
          let qs = require('qs');
          $this.$ajax.post("collection/getcollections", qs.stringify(obj), {
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded'
            }
          })
          .then(function(rpdata) {
            if(rpdata.data.meta.code=='200'){
              if(rpdata.data.data.data.length>0){
                $this.collectList = $this.collectList.concat(rpdata.data.data.data);
                $this.pageNum++;
                $this.loading = false;
              }else{
                $this.finished = true;
                $this.loading = false;
              }
            }
          })
          .catch(function(err) {})
        }, 1000);
      },
		}
	}
</script>
<style scoped>
  .login_box{
    margin-top: 54px;
  }
  #userCollect .van-card__thumb {
    top: 0;
    left: 0;
    width: 90px;
    height: 90px;
    position: absolute;
    overflow: hidden;
  }
  .van-card {
    color: #333;
    height: 100px;
    font-size: 16px;
    background: #fafafa;
    position: relative;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding: 10px 15px 5px 115px;
  }
</style>



